.card-list-items {
    margin-top: 10px;
    margin-bottom: 5px;
}

.card-list-items--spinner {
    height: 500px;
    line-height: 500px !important;
}

.card-list-items--default--cost {
    font-weight: 700;
    color: #44a9ff;
    margin: 5px 0;
    font-size: calc((100vw - 480px) / (1280 - 480) * (18 - 16) + 16px) !important;
}

.card-list-items--choose {
    margin: 15px 0;
}

.ant-card-meta-title, .ant-input-group-addon {
    color: #33475b !important;
    font-size: calc((100vw - 480px) / (1280 - 480) * (17 - 16) + 14px);
}

.card-list-items--btn {
    width: 100%;
    margin-top: 15px;
    text-align: center;
    font-size: calc((100vw - 480px) / (1280 - 480) * (16 - 16) + 14px) !important;
}


